import React from 'react';
import { Modal, Box, Paper, Divider, Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';

interface IDetail extends IndexMap<string | number> {
    id:  number | string;
    activity_done_day: string | number;
    activity_task: string;
    done_state: string | number;
    done_state_label: string;
    drafttype: string;
    drug_name: string;
    eartag: string;
    event_date: string;
    pen_name: string;
    rem: string;
    technician: string;
    workflow_type_name: string;
}
interface IProps {
    open: boolean;
    setOpen: (open: boolean) => void;
    detail: IDetail;
}
const DetailMap: IndexMap<string> = {
    activity_done_day: '',
    activity_task: '',
    done_state_label: '',
    drafttype: '',
    drug_name: '',
    eartag: '',
    event_date: '',
    pen_name: '',
    rem: '',
    technician: '',
    workflow_type_name: ''
};
const DetailFields = [
    'activity_task',
    'drafttype',
    'workflow_type_name',
    'eartag',
    'pen_name',
    'drug_name',
    'technician',
    'event_date',
    'activity_done_day',
    'rem',
    'done_state_label'
];

const useStyles = makeStyles((theme: any) => ({
    content: {
        position: 'absolute',
        top: '50%',
        left: '50%',
        width: '80%',
        transform: 'translate3d(-50%,-50%, 0)',
        backgroundColor: '#fff',
        borderRadius: 5,
        padding: '0 10px 10px',
        lineHeight: 2
    },
    label: {
        color: '#333',
        marginRight: 5
    },
    value: {
        color: theme.palette.primary.main
    }
}))
export default function Detail(props: IProps) {
    const classes = useStyles();
    const { open, setOpen, detail } = props;
    return (
        <Modal
            aria-labelledby="simple-modal-title"
            aria-describedby="simple-modal-description"
            open={open}
            onClose={() => setOpen(false)}
        >
            <Paper className={classes.content}>
                <Box className="text-center" fontSize="1.2rem"></Box>
                {
                    DetailFields.map(item => (
                        <Box key={item}>
                            <Divider color="primary" />
                            <Grid container>
                                <Grid item xs="auto">
                                    <div className={clsx(classes.label, 'text-left')}>
                                        {DetailMap[item]}:
                                    </div>
                                </Grid>
                                <Grid item xs>
                                    <div className={clsx(classes.value, 'text-right')}>
                                        {detail[item]}
                                    </div>
                                </Grid>
                            </Grid> 
                        </Box>
                    ))
                }
            </Paper>
        </Modal>
    )
}